.card-container{
    display: flex;
    flex-direction: column;
    margin-left: 5rem;
    font-family: "Droid Serif", serif;
  }
  
  .card-container .heading{
    font-size: 35px;
    font-weight: 600;
    margin-bottom: 3rem;
    
  }
  
  .cardholder{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
  }
  
  .card{
    margin-right: 6rem;
    margin-bottom: 5rem;
    width: 23rem;
    height: 19rem;
  }
  
  
  @media screen and (max-width: 768px){
    .card-container{
      margin-left:10rem;
    }

  
    .card{
      margin-bottom: 2rem;
    }
  }