.slick-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width:100vw;
    height:50vh;
    margin-bottom: 15rem;
}

.slick-slider-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  


.glide-container{
    margin-left: 3rem;
    margin-top: 2rem;
    width:50vw;
    height:50vh;
}

.glide-container .main-image{
    margin-left: 1rem;
    width: 48vw;
    height: 52vh;
}

.glide-container .small{
   
    width: 14rem;
    height: 10rem;
}
.small{
    margin-left: 1rem;
}
.slick-next .slick-prev{
    background-color: #0067A5;
    color: white;
    width: 40px;
    height: 46px;
}

.slick-prev:before, .slick-next:before {
    font-family: 'slick';
    font-size: 34px;
    line-height: 1;
    opacity: .75;
    color: blue;
}
